body {
  height: 100vh;
  font-family: Lucida Sans, Lucida Sans Regular, Lucida Grande, Lucida Sans Unicode, Geneva, Verdana, sans-serif;
  font-size: 14px;
  font-weight: 100;
}

.link {
  color: #000;
  text-decoration: none;
}

.search-form {
  cursor: pointer;
  background-color: #0ff;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.input {
  border-radius: 5%;
  margin-right: 10px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-evenly;
  display: flex;
}

.load-more {
  color: #000;
  background-color: #0ff;
  border-radius: 10%;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  font-weight: 700;
  transition-property: background-color;
  transition-duration: .3s;
  transition-timing-function: linear;
  display: none;
}

.load-more:hover {
  background-color: #ff0;
}

.btn-submit {
  color: #000;
  background-color: #0ff;
  border-radius: 10%;
  padding: 10px;
  font-weight: 700;
  transition-property: background-color;
  transition-duration: .3s;
  transition-timing-function: linear;
}

.btn-submit:hover {
  background-color: #ff0;
}

.photo-card {
